$secondary-font-size: 1rem;
$secondary-font-weight: 400;
$secondary-font-color: #222222;
$font-family: "Verdana";

$secondary-font-color-dark: #818181;

$color-button: #f2f2f2;
$color-button-dark: #3b3b3b;

$color-primary: #e3e3e3;
$color-secondary: #dbdbdb;
$color-tertiary: #d3d3d3;

$color-tertiary-dark: #272727;
$color-secondary-dark: #2c2c2c;
$color-primary-dark: #333333;



.container-h {
  display: inline-block;
  margin: 0px 10px;
}

.ui-block {
  height: 500px;
  padding: 25px;
  background-color: $color-secondary;
  border-radius: 15px;
  box-shadow: 0 0px 15px 0 #a1a1a1;
  &.dark {
    background-color: $color-secondary-dark;
    box-shadow: 0 0px 15px 0 #171717;
  }
}
